.list {
	display: flex;
	flex-direction: column;
	gap: 10px;
  }
  
  .item {
	display: flex;
	font-size: 18px;
	align-items: center;
	padding-top: 10px;
	gap: 10px;
  }
  
  .btn {
	background: #fff;
	border: 1px solid rgba(5,5,5,.2);
	border-radius: 5px;
	color: red;
	cursor: pointer;
	font-size: 14px;
	padding-left: 10px;
	padding-right: 10px;
	text-transform: capitalize;
	transition: background-color var(--trans-hv);
  }
  
  .btn:hover {
	background-color: red;
	color: #fff;
  }